  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  position: relative;
  width: 100%;
  min-height: 100%;
  margin: 0 auto;
  background-color: #fff;
  font-family: 'roboto_condensedregular', sans-serif;
  text-transform: uppercase; }

a {
  text-decoration: none; }

@font-face {
  font-family: 'roboto_condensedregular';
  src: url("../fonts/robotocondensedregular-webfont.woff2") format("woff2"), url("../fonts/robotocondensedregular-webfont.woff") format("woff");
